<template>
  <div id="app">
    <header>
		<h1>Welcome! Please, calculate your credit.</h1>
	</header>
	<section class="wrapper">
		<section class="calculate">
			<form action="submit" class="calculate__form">
				<label class="calculate__form__item">Стоимость
					<span class="calculate__form__item__container">
						<b-form-input type="text" name="cost" 
            v-model="calculator.cost" 
            v-on:input.native="isInputNumber"
            lazy-formatter
            :formatter="formatter"
            placeholder="Введите стоимость" 
            required></b-form-input>
						<span>&#8381</span>
					</span>

				</label>
				<label class="calculate__form__item">Первоначальный взнос
					<span class="calculate__form__item__container">
						<b-form-input type="text" name="firstPayment" 
            v-model="calculator.firstPayment" 
            v-on:input.native="isInputNumber"
            lazy-formatter
            :formatter="formatter"
            placeholder="Введите сумму" required></b-form-input>
						<span>&#8381</span>
					</span>
					
				</label>

				
        <b-form-radio-group
          v-model="selected"
          :options="options"
          name="buttons-1"
          size="lg"
          class="w-100"
          v-on:change.native="activateRadioButton"
          buttons
        ></b-form-radio-group>
			

				<label class="calculate__form__item">Срок кредита
					<span class="calculate__form__item__container">
						<b-form-input type="text" name="term" 
            v-model="calculator.term" 
            v-on:input.native="isInputNumber"
            lazy-formatter
            :formatter="formatter" 
            placeholder="Введите количество лет" required></b-form-input>
						<span>лет</span>
					</span>
				</label>
				<label class="calculate__form__item">Процентная ставка
					<span class="calculate__form__item__container">
						<b-form-input type="text" name="rate" 
            v-model="calculator.rate" 
            v-on:input.native="isInputNumber"
            lazy-formatter
            :formatter="formatter"
            placeholder="Введите ставку" 
            required></b-form-input>
						<span>&#8381</span>
					</span>
				</label>
				<div class="button-wrapper">
					<b-button type="submit" class="button" variant="success" 
          v-on:click.prevent="persist">Save</b-button>
					<b-button type="button" class="button" variant="outline-secondary" v-on:click="resetForm">Clear</b-button>
				</div>
				
			</form>
		</section>
		<section class="result">
      <b-card-group desk class="row">
        <b-card class="result-card" border-variant="success" header="Ежемесячный платеж:" align="center">
          <b-card-text>{{(calcMonthPayment ? calcMonthPayment : 0) | formatNumber}}</b-card-text>
        </b-card>

        <b-card class="result-card" border-variant="success" header="Необходимый доход:" align="center">
          <b-card-text>{{(calcUncome ? calcUncome : 0) | formatNumber}}</b-card-text>
        </b-card>

        <div class="w-100"></div>

        <b-card class="result-card" border-variant="success" header="Переплата:" align="center">
          <b-card-text>{{(calcOverPayment ? calcOverPayment : 0) | formatNumber}}</b-card-text>
        </b-card>

        <b-card class="result-card" border-variant="success" header="Тело кредит:" align="center">
          <b-card-text>{{(calcCreditBody ? calcCreditBody : 0) | formatNumber}}</b-card-text>
        </b-card>

      </b-card-group>
		</section>
	</section>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data() {
    return {
    calculator: {
      cost: ``,
      firstPayment: ``,
      term: ``,
      rate: ``,
      },

    selected: '',
        options: [
          { text: '10%', value: 10/100 },
          { text: '15%', value: 15/100 },
          { text: '20%', value: 20/100 },
          { text: '25%', value: 25/100 },
          { text: '30%', value: 30/100 }
        ]
    }
  },

  mounted() {
    for (let key in this.calculator) {
      if (key !== null) {
        this.calculator[key] = localStorage.getItem(key)
      }
    }
  },

  computed: {
    persist() {
      localStorage.setItem(`cost`, this.calculator.cost);
      localStorage.setItem(`firstPayment`, this.calculator.firstPayment);
      localStorage.setItem(`term`, this.calculator.term);
      localStorage.setItem(`rate`, this.calculator.rate);
    },

    isFormFull: function() {
      if (this.calculator.cost, this.calculator.firstPayment, this.calculator.term, this.calculator.rate) {
        return true
      }
    },

    getMonths: function() {
      return this.calculator.term * 12
    },

    calcCreditBody: function() {
      if (this.calculator.cost && this.calculator.firstPayment) {
      return Math.round(this.calculator.cost.replace(/\s+/g, '') - this.calculator.firstPayment.replace(/\s+/g, ''));
      }
    },

    calcMonthPayment: function() {
      if (this.isFormFull) {
        const rateInd = this.calculator.rate / 1200;
        return Math.round(this.calcCreditBody * (rateInd + rateInd / (Math.pow(1 + rateInd, this.getMonths) - 1)))
      }
    },

    calcUncome: function() {
      if (this.isFormFull) {
        return Math.round(5 * this.calcMonthPayment / 3)
      }
    },
    calcOverPayment: function() {
      if (this.isFormFull) {
        const val = this.calcMonthPayment * this.getMonths - this.calculator.cost.replace(/\s+/g, '')* 1 + this.calculator.firstPayment.replace(/\s+/g, '') * 1;
        return val;
      }
    },
  },

  methods: {
    activateRadioButton(event) {
      if (this.calculator.cost) {
        let formatedCost = this.calculator.cost.replace(/\s+/g, '')
        let newValue = Math.round(formatedCost * this.selected) + ``;
        this.calculator.firstPayment = newValue.replace(/(\d)(?=(\d{3})+([^\d]|$))/g, '$1 ');
      } else {
        const inputCost = document.querySelector('input[name=cost]');
        inputCost.setCustomValidity('Введите стоимость недвижимости');
        inputCost.reportValidity();
      }
    },

    resetForm() {
      for (let key in this.calculator) {
        this.calculator[key] = ``;
      }
      localStorage.clear();
    },

    isInputNumber(event) {
      this.selected = ``;

      if (Number(event.target.value.replace(/\s+/g, '')) !== Number(event.target.value.replace(/\s+/g, ''))) {
        event.target.setCustomValidity(`Введите числовое значение!`);
        event.target.reportValidity();
      }
      else {
        event.target.setCustomValidity(``);
        event.target.reportValidity();
      }
    },

    formatter(value) {
      return value.replace(/\s+/g, '').replace(/(\d)(?=(\d{3})+([^\d]|$))/g, '$1 ');
    },

  },
  filters: {
    formatNumber(value) {
      return value.toLocaleString('ru')
    },
  }
}

</script>

